
body {
  background: #222;
  color: white;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100vh;
}
svg {
  background: black;
}
line {
  stroke: black;
}
main {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
}
textarea{
  background: black;
  color: white;
}
:root{
  --stroke-width: 1px;
}
path {
  stroke-width: var(--stroke-width);
}
input[type="range"] {
  -webkit-appearance: none;
  -moz-appearance: none;
       appearance: none;
  background-color: black;
  height: 2px;
  width: 100%;
  border-radius: 6px;
}




